.toggle {
  position: relative;
  width: 35px;
  height: 35px;
  appearance: none;

  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 35px;
    height: 1.5px;
    background-color: #000000;
    transition: all 0.5s ease;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 2px;
  }

  &::before {
    top: 8px;
  }

  &::after {
    bottom: 8px;
  }
}

.toggle__inner {
  position: absolute;
  display: block;
  top: 50%;
  left: 50%;
  width: 35px;
  height: 1.5px;
  background-color: #000000;
  transform: translate(-50%, -50%);
  transition: all 0.5s ease;
  border-radius: 2px;
}

