.form {

}

.form__row {
  display: flex;
  flex-direction: column;

  @include desktop() {
    flex-direction: row;
  }

  & + .form__row {
    margin-top: 15px;
    @include desktop() {
      margin-top: 20px;
    }
  }
}

.form__block {
  box-sizing: border-box;
  flex: 1;
  border: 1px solid #BDBDBD;
  border-radius: 12px;
  padding: 17px;
  transition: .3s;

  & + .form__block {
    margin-top: 15px;

    @include desktop() {
      margin-top: initial;
      margin-left: 20px;
    }
  }
}
.form__block.error {
  border: 1px solid #FF650E;
}

.form__input-error {
    display: none;
    text-align: center;
    font-size: 9px;
    line-height: 9px;

    @include desktop {
      font-size: 10px;
      line-height: 14px;
    }

  &.active {
    display: block;
  }
}

.form__message {
  font-size: 16px;
  font-weight: 700;
  margin-top: 5px;
}



.form__btn {
  padding: 17px;

  @include desktop {
    padding: 23px;
  }
}

.form__politics {
  color: #4E4E4E;
  font-size: 14px;

  @include desktop() {
    font-size: 15px;
  }

  a {
    text-decoration: underline;
  }
}
